
#app2 .tab-bar{
    display: flex;
}
#app2 .tab-bar > li{
    border: 1px solid black;

    width: 50%;
}
#app2 .tab-bar > li.selected{
    background-color: #FFCA28;
    color: #fff;
}
#app2 .tab-content > li{
    display: none;
}
#app2 .tab-content > li.active{
    display: block;
}